import { useTranslation } from "react-i18next";
import style from "../../../StoreManager/StoreManager.module.css";
import FormFileView from "../../../../Ui/Input/FormFileView";
import { useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import DepartmentSiteStudentAssociationCardMenu from "./DepartmentSiteStudentAssociationCardMenu";
const DepartmentSiteStudentAssociationCard = ({ pso, onRefetch, dsid }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div
        className={style.smc_card}
        style={{
          alignItems: "flex-start",
        }}
      >
        <div className={style.smc_card_contatiner}>
          <div className={style.smc_card_contatiner}>
            <div className={style.smc_card_contatiner_inner}>
              <h6>
                {" "}
                {t("name")} : {pso?.title}
              </h6>
              <FormFileView
                label={`${t("image")}: `}
                value={{
                  key: pso?.image,
                }}
                customFormInputContainer={{
                  margin: "0",
                }}
              />
              <p>{pso?.description ?? ""}</p>
            </div>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
        />
      </div>
      {open && (
        <DepartmentSiteStudentAssociationCardMenu
          onClose={onToggle}
          dsid={dsid}
          menuData={pso}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default DepartmentSiteStudentAssociationCard;
