import { useEffect, useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useDepartmentAddExtraData } from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/Income/Income.module.css";
import FormTextarea from "../../../../Ui/Input/FormTextarea";
import { useFileUpload } from "../../../../Hooks/ApiHooks/Auth/auth-api";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../../../ImageCrop/CustomCrop";
import FormFile from "../../../../Ui/Input/FormFile";
const AddDepartmentSiteStudentAssociation = ({
  onClose,
  onRefetch,
  dsid,
  onEditAction,
  menuData,
  isEdit,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    title: "",
    description: "",
    image: "",
  });
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");

  const [departmentAddExtraData] = useDepartmentAddExtraData();
  useEffect(() => {
    if (isEdit && menuData) {
      setState({
        title: menuData?.title ?? "",
        description: menuData?.description ?? "",
        image: menuData?.image ?? "",
      });
    }
  }, [isEdit, menuData]);
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const [fileUpload] = useFileUpload();
  const onCrop = (fl, url) => {
    setOpenCrop(false);
    onFileUploadCrop(fl);
  };

  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            image: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, ["image", "description"]);
    if (existingRequiredField(validation) && dsid) {
      if (isEdit) {
        onEditAction(state);
      } else {
        setDisabled((pre) => !pre);
        departmentAddExtraData({
          dsid: dsid,
          bodyParameter: {
            ...state,
            flow: "STUDENT_ASSOCIATIONS",
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  //   console.info("fdsfgfsd", dsid, errorField);

  return (
    <>
      {openCrop ? (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={1}
            second={1}
            onCrop={onCrop}
          />
        </Dialog>
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {t(
                      isEdit
                        ? "update_student_association"
                        : "add_student_association"
                    )}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("title")}
                placeholder={t("title_placeholder")}
                name={"title"}
                value={state.title}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["title"]}
              />
              <FormFile
                label={t("add_image")}
                name={"home-opener"}
                value={
                  state.image
                    ? {
                        key: state.image,
                        name: "View",
                      }
                    : ""
                }
                onChange={onFileUpload}
                errorShow={errorField["image"]}
              />
              <FormTextarea
                label={t("description")}
                placeholder={t("description_placeholder")}
                name="description"
                type="text"
                value={state.description}
                onChange={onChange}
                customFormInputContainer={{
                  width: "100%",
                }}
                errorShow={errorField["description"]}
              />
              <CreateButton
                label={isEdit ? "update" : "add"}
                onAction={onAdd}
              />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddDepartmentSiteStudentAssociation;
